<template>
  <v-container>
    <v-card elevation="5">
      <v-card-text class="text-center">
        <h3>Formulario Solicitud Toma Muestras a Domicilio</h3>
      </v-card-text>
      <v-card-text class="text-center">
        <v-form ref="form" v-model="valid">
          <v-text-field
            dense
            outlined
            v-model="datos.nombre"
            label="Nombre Completo"
            :rules="[(v) => !!v || 'Debe indicar el nombre']"
          ></v-text-field>
          <v-text-field
            dense
            outlined
            v-model="datos.documento"
            label="Documento de Identidad"
            :rules="[(v) => !!v || 'Debe indicar documento de identidad']"
          ></v-text-field>
          <v-text-field
            dense
            outlined
            v-model="datos.telefono"
            label="Tel√©fono de Contacto"
            :rules="[(v) => !!v || 'Debe indicar un tel√©fono de contacto']"
          ></v-text-field>
          <v-text-field
            dense
            outlined
            v-model="datos.direccion"
            label="Direcci√≥n"
            :rules="[(v) => !!v || 'La direcci√≥n es requerida']"
          ></v-text-field>
          <v-text-field
            dense
            outlined
            v-model="datos.entidad_salud"
            label="Entidad de Salud que lo remite"
            :rules="[(v) => !!v || 'Debe indicar la entidad de salud que lo remite']"
          ></v-text-field>
          <v-text-field
            dense
            outlined
            v-model="datos.examenes_realizar"
            label="Ex√°men(es) a realizar"
            :rules="[(v) => !!v || 'Debe indicar los ex√°menes a realizar']"
          ></v-text-field>
          <v-btn color="primary" @click="solicitarTomaMuestras()">Solicitar</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      valid: false,
      datos: {
        nombre: "",
        documento: "",
        telefono: "",
        direccion: "",
        entidad_salud: "",
        examenes_realizar: "",
      },
    };
  },
  computed: {
    ...mapState(["imageAgent", "emailTo"]),
  },
  methods: {
    ...mapActions(["sendMessageUser", "sendEmail"]),
    solicitarTomaMuestras() {
      if (!this.$refs.form.validate()) {
        console.log("no valido");
        return;
      }
      const datosEmail = `
        <div>
          <table>
            <tbody>
              <tr>
                <td><b>Nombre Completo</b></td>
                <td>${this.datos.nombre}</td>
              </tr>
              <tr>
                <td><b>Documento de Identificaci√≥n</b></td>
                <td>${this.datos.documento}</td>
              </tr>
              <tr>
                <td><b>Tel√©fono</b></td>
                <td>${this.datos.telefono}</td>
              </tr>
              <tr>
                <td><b>Direcci√≥n</b></td>
                <td>${this.datos.direccion}</td>
              </tr>
              <tr>
                <td><b>Entidad de Salud que lo remite</b></td>
                <td>${this.datos.entidad_salud}</td>
              </tr>
              <tr>
                <td><b>Ex√°menes a realizar</b></td>
                <td>${this.datos.examenes_realizar}</td>
              </tr>
            </tbody>
          </table>
        </div>
      `;
      this.sendEmail({
        to: this.emailTo,
        message: datosEmail,
        subject: "Solicitud Toma de Muestras a Domicilio",
      });
    },
  },
};
</script>

<style>
/* Alto de los inputs */
.v-input__control {
  height: 70px;
}
</style>